@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;300&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;height:100vh;width:100%;overflow:hidden;font-weight:200;display:flex;justify-content:center;align-items:center;background:#676681;background:linear-gradient(0deg, #131830 0%, #333150 100%)}main{display:flex;justify-content:space-around;flex-direction:column;height:calc(100% - 92px)}.d-block{display:block}.pointer{cursor:pointer}

.general{display:flex;justify-content:center;align-items:center;width:100vw;min-height:100vh}.container{width:380px;height:670px;border-radius:10px;padding:1em;overflow:hidden;position:relative}@media screen and (max-width: 380px){.container{width:100%;height:100vh;border-radius:0}}

.loading-container{display:flex;justify-content:center;align-items:center;height:100%;width:100%}.lds-ripple{display:inline-block;position:relative;width:80px;height:80px}.lds-ripple div{position:absolute;border:4px solid #fff;opacity:1;border-radius:50%;-webkit-animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.lds-ripple div:nth-child(2){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@-webkit-keyframes lds-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}@keyframes lds-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}

.burger-menu{cursor:pointer;outline:none;position:relative;width:20px;height:7px;transition:all 0.25s ease-in-out}.burger-menu .line1,.burger-menu .line2{content:'';position:absolute;height:1px;width:100%;transition:all 0.25s ease-in-out;border-radius:3px}.burger-menu .line1{right:0;bottom:0%;width:80%}.burger-menu .line2{top:0}.menu-active{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.menu-active .line{opacity:0}.menu-active.burger-menu .line1{-webkit-transform:rotate(-90deg);transform:rotate(-90deg);bottom:43%;width:100%}.menu-active.burger-menu .line2{top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}

.input-container{display:flex;justify-content:center;align-items:center;position:absolute;right:8%;top:10%;-webkit-animation:slideUp 0.5s ease;animation:slideUp 0.5s ease}@-webkit-keyframes slideUp{0%{opacity:0;-webkit-transform:translateY(20px);transform:translateY(20px)}60%{opacity:1;-webkit-transform:translateY(-5px);transform:translateY(-5px)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes slideUp{0%{opacity:0;-webkit-transform:translateY(20px);transform:translateY(20px)}60%{opacity:1;-webkit-transform:translateY(-5px);transform:translateY(-5px)}100%{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}.country-select{background:none;border:none;outline:none;color:#fff;font-family:'Inter', sans-serif;border-bottom:0.5px solid #a1a8bb;padding-bottom:5px;width:10em}.country-select::-webkit-input-placeholder{color:#a1a8bb}.country-select:-ms-input-placeholder{color:#a1a8bb}.country-select::-ms-input-placeholder{color:#a1a8bb}.country-select::placeholder{color:#a1a8bb}.btn-country{background:none;border:none;margin-left:0.5em;margin-top:-6px;cursor:pointer;outline:none}.search.icon{color:#a1a8bb;position:relative;margin-top:2px;margin-left:3px;width:12px;height:12px;border:solid 1px currentColor;border-radius:100%;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.search.icon:before{content:'';position:absolute;top:12px;left:5px;height:6px;width:1px;background-color:currentColor}

.top-bar{width:100%;display:flex;justify-content:space-between;padding:1em}.btn-container{cursor:pointer;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

.graficos{margin:0 auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:2em 0;position:relative}img{width:200px}.resplandor{-webkit-animation:titilarLuz 2s ease infinite alternate;animation:titilarLuz 2s ease infinite alternate}@-webkit-keyframes titilarLuz{0%{-webkit-filter:drop-shadow(0 0 10px rgba(251,199,142,0.3));filter:drop-shadow(0 0 10px rgba(251,199,142,0.3))}100%{-webkit-filter:drop-shadow(0 0 30px rgba(251,199,142,0.5));filter:drop-shadow(0 0 30px rgba(251,199,142,0.5))}}@keyframes titilarLuz{0%{-webkit-filter:drop-shadow(0 0 10px rgba(251,199,142,0.3));filter:drop-shadow(0 0 10px rgba(251,199,142,0.3))}100%{-webkit-filter:drop-shadow(0 0 30px rgba(251,199,142,0.5));filter:drop-shadow(0 0 30px rgba(251,199,142,0.5))}}


.nube{position:absolute;bottom:7%;left:4%}.nube:nth-child(2){width:70px;top:7%;left:inherit;right:10px}.nube--primera{width:70px;top:7%;left:inherit;right:10px}.nube--segunda{width:100px;bottom:7%;left:4%}

.nube-clara{z-index:2;position:relative;right:10%}.nube-clara:nth-child(2){position:absolute;width:150px;right:-20%;z-index:1}

.nube-oscura{z-index:2;position:relative;right:10%}.nube-oscura:nth-child(2){position:absolute;width:150px;right:-20%;z-index:1}

.nube-lluvia{position:relative;z-index:2}.nube-lluvia:nth-child(2){position:absolute;width:50px;right:-20px}.lluvia{display:flex;justify-content:space-between;align-items:flex-end;width:120px;position:absolute;bottom:-60px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:1}.lluvia img{width:12px}

.sol-lluvia{width:80px;position:absolute;z-index:1;right:10px;top:20px}.luna-lluvia{width:80px;position:absolute;z-index:1;right:10px;top:20px}.nube-lluvia-con-sol{position:relative;z-index:2}.lluvia{display:flex;justify-content:space-between;align-items:flex-end;width:120px;position:absolute;bottom:-60px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:1}.lluvia img{width:12px}

.nube-oscura-rayo{z-index:2;position:relative;right:10%}.nube-oscura-rayo:nth-child(2){position:absolute;width:150px;right:-20%;z-index:1}.rayo{width:70px;position:absolute;bottom:-30px;left:25%;z-index:1}

.nube-nieve{position:relative;z-index:2}.nieve{display:flex;justify-content:space-between;align-items:flex-end;width:120px;position:absolute;bottom:-60px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:1}.nieve img{width:12px}

.nube-niebla{position:relative;z-index:2}.niebla{width:150px;position:absolute;bottom:-20px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);z-index:1}.niebla img{width:100%}

.graficos--container{display:flex;align-items:center;margin:0 auto;height:350px;width:80%}

.actual .pais{font-size:0.9em}.actual .temperatura{font-size:4.5em;font-weight:300}.actual .termica{font-size:0.7em}

.info-clima-horario{margin:0 0px;display:flex;justify-content:center;align-items:center;flex-direction:column;width:65px}.info-clima-horario:last-child{margin:0}.info-clima-horario .horario{font-size:0.9em}.nube-prox-horario{width:32px;margin:0.5em 0}

.proxima{display:flex;font-weight:300}

.clima-info{padding:1em;display:flex;justify-content:space-between;align-items:flex-end}

